let countrySelect = document.querySelectorAll(".country-select")

countrySelect.forEach(el => {
    let toggleBlock = el.querySelector(".country-select__show")
    let current = el.querySelector(".country-select__current")
    let options = el.querySelectorAll(".country-item")

    function close(){
        el.classList.remove("_open")
    }

    toggleBlock.onclick = function (){
        el.classList.toggle("_open")
    }

    options.forEach(option => {
        option.onclick = function () {
            let img = option.getAttribute("data-img")
            let title = option.getAttribute("data-title")
            close()

            current.innerHTML = `
                <img src="${img}" alt="" class="country-item__img">
                <div class="country-item__title">
                    ${title}
                </div>
            `
        }
    })

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(el);

        if ( ! withinBoundaries ) {
            close()
        }
    })
})


let benefitsModal = document.querySelector(".benefits-modal")
let benefitsModalBtn = document.querySelector("._show-benefits-modal")
let benefitsModalNav = document.querySelector(".benefits__nav")

if(benefitsModalBtn){
    benefitsModalBtn.onclick = function () {
        benefitsModal.classList.add("_open")

        setTimeout(()=>{
            document.addEventListener( 'click', (e) => {
                const withinBoundaries = e.composedPath().includes(benefitsModalNav);

                if ( ! withinBoundaries ) {
                    if(benefitsModal.classList.contains("_open")){
                        benefitsModal.classList.remove("_open")
                    }
                }
            })
        }, 1000)
    }
}

