function gallerySlider() {
    var swiper = new Swiper('.gallery .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: `.gallery .swiper-button-next`,
            prevEl: `.gallery .swiper-button-prev`,
        },
    })
}
gallerySlider()
